import classes from "./LeftSideBar.module.css";
import React from "react";

import LeftSideBarBox from "./LeftSideBarBox/LeftSideBarBox";

const LeftSideBar = (props) =>{
    // This props take in a data
    // This data must be in the following format
    // const iOSAnimationSBData = [
    //       { headerTitle: "Other Courses",  
    //         subText: ["iOS Development for Beginners", 
    //          "Push Notification"]
    // }, ...]
    //
    // state

    let sidebarData = props.data;
    
    return(
        <div className = {[classes.LeftSideBar,
                           props.state === 1 ? classes.notHidden : null].join(' ')}>
                <a href = {props.headerURL}><p className = {classes.Main}>{props.header}</p></a>
                {props.state === 1 ? <LeftSideBarBox index = {2}
                                activate = {false}
                                header = "Reference" 
                                subtexts = {[{  subTextName: "Swift", 
                                                subTextHref: "/reference/swift" } ]}/> : null }
                {props.state === 1 ? <LeftSideBarBox index = {2}
                                activate = {false}
                                header = "Courses" 
                                subtexts = {[{  subTextName: "iOS Animation", 
                                                subTextHref: "/course/iOSAnimation"} ]}/> : null }
                {
                    sidebarData.map( (value, index) => {
                        return(
                            <LeftSideBarBox key = {index}
                                            index = {index}
                                            activate = {props.activate}
                                            length = {sidebarData.length}
                                            header = {value.headerTitle} 
                                            subtexts = {value.subText}/>
                        )
                    })
                }

                <br/>
                <br/>
                <br/>
                <br/>
                <br/>
        </div>
    )
};

export default LeftSideBar;