import React from "react";
import classes from "./RoundedButton.module.css";
import Youtube from "../../../../Assets/Social Media Icon/Youtube.png";

const RoundedButton = (props) => {

    
    return(
        <a className = {[classes.FilledButton, 
                         props.color === "red" ? classes.red : classes.blue].join(' ')} href = {props.buttonhref}>
                         {props.color === "red" ? <img src = {Youtube} alt = "Youtube"/> : null}
                         {props.buttonTitle}</a>
    )
};

export default RoundedButton;