import React, {useState} from 'react';
import classes from "./LeftSideBarBox.module.css";
import {NavLink} from "react-router-dom";
import DropdownArrow from "../../UI/Dropdown Arrow/DropdownArrow";

const LeftSideBarBox = (props) => {
    let open = false;
    if (props.index === props.activate[0]) {
        open = true
    }

    let [clicked, setClicked] = useState(open)
    let updateClicked = new Array(props.subtexts.length).fill(false)

    const openBox = () => {
        setClicked(!clicked)
        console.log(clicked)
    };

    if (props.index === props.activate[0]) {
        updateClicked[props.activate[1]] = true
    }

    return(
        <div className = {classes.Block}>
            <div className = {classes.HeaderDiv} 
                 onClick = {openBox}
                 >
                <button className = {classes.Header}>{props.header}</button>
                <DropdownArrow activate = {true}/>
            </div>
            {props.subtexts.map( (value, index) => {
                return(
                    <NavLink key = {index} 
                       className = {classes.subTextA} 
                    to = {value.subTextHref}>
                    <p key = {index} 
                       className = {[classes.Subtext, 
                                     clicked ? null : classes.hidden,
                                     updateClicked[index] ? classes.Activate : null].join(' ')} >
                                     {value.subTextName}</p>
                    </NavLink>
                )
            })}

        </div>
    )
};

export default LeftSideBarBox;