import React from "react";
import classes from "./dropback.module.css";

const dropback = (props) => {

    let size = null;

    if (props.size === 1){
        size = classes.sideBarsize
    }

    return(
        <div className = {[classes.dropback, size].join(' ')} 
             onClick = {props.dropbackClicked}
             >

        </div>
    )
}

export default dropback;