import React from 'react';
import classes from './AuthorInfo.module.css';

const AuthorInfo = (props) => {
    // Props Required:
    // name: Author name. e.g Wes Chua Swee Kwang
    // info: e.g 12 Nov 2020  Online Video (20 hrs)  Intermediate
    // version: e.g Xcode 12.2, Swift 5.3
    return(
        <div className = {classes.AuthorInfo}>
            {props.name !== undefined ? <p>By: <a href = {props.href} target = "_blank" rel="noreferrer" >{props.name}</a></p> : null }
            {props.info !== undefined ? <p>Last update: {props.info}</p> : null}
            {props.version !== undefined ? <p className = "lightGrey">Tested on: {props.version}</p> : null}
        </div>
    )
};

export default AuthorInfo;