import React, {Fragment} from "react";
import {Helmet} from "react-helmet";
import Constant from "../../../../../Constant";

import WithSidebar from "../../../../../Container/WithSidebar/WithSidebar";
import AuthorInfo from "../../../../../Components/AuthorInfo/AuthorInfo";
import Swift from "../Swift";
import SwiftCode from "../../../../../Components/SwiftCode/SwiftCode";

const String = (props) => {
    return(
        <Fragment>
            <Helmet>
                <title>String Concatenation and Interpolation in Swift | Appdevelopments.co</title>
                <meta name="description" content="Arithmetic operations are used to turn a data into another 
                 piece of data. The simplest math operations are addition, subtraction, mutiplication and division. 
                 Moreover, you will also learn about Compound Assignment Operators, Increment and Decrement."/>
                <meta name="keywords" content="String,Concatenation,slicing,Interpolation,create,line,number,iOS,swift,tutorial,video,reference"/>
                <meta name="author" content="Wes Chua Swee Kwang"/>
                <link href={Constant.address + "/reference/swift/arithemtic"} rel="canonical"/>
                <link href={Constant.address} rel="home"/>
                <meta name="robots" content="index, follow"/> 
            </Helmet>

            <WithSidebar 
                data = {Swift}
                headerTitle = "Swift Reference"
                headerURL = "/reference/swift/string"
                activate = {[2,4]} // Section, Row
                state = {props.state}>

                <h1 className = "subHeader">String Concatenation and Interpolation</h1>
                <h2 className = "header">String in Swift</h2>
                <AuthorInfo
                        version = "Xcode 12.2, Swift 5.3"/>
                <p>A string is a collection of characters. For example, the word "Wes" contains the follow 
                   characters "W", "e", "s".</p>
                <h3>How to create String?</h3>
                <p>To create string, you use double quotation marks like the one shown below: </p>
                <SwiftCode 
                    code = {<Fragment>              
                        <span className = "darkpurple">let</span> <span className = "blue">myName</span> = <span className = "red">"Wes"</span> 
                    </Fragment>} />
                <p><strong>Multiline string</strong> can be create using triple quotation marks.</p>
                <SwiftCode 
                    code = {<Fragment>              
                        <span className = "darkpurple">let</span> <span className = "blue">multi</span> = <span className = "red">" This <br/>
                        &nbsp;&nbsp;&nbsp;&nbsp;is <br/>
                        &nbsp;&nbsp;&nbsp;&nbsp;a  <br/>
                        &nbsp;&nbsp;&nbsp;&nbsp;multiline <br/>
                        &nbsp;&nbsp;&nbsp;&nbsp;string <br/>
                        &nbsp;&nbsp;&nbsp;&nbsp;"</span> 
                    </Fragment>} />
                <h3>String Concatenation</h3>
                <p>String Concatenation is to combine two or more string together. As shown below, string 
                   concatenation will not add any spaces for you.</p>
                <SwiftCode 
                    code = {<Fragment>              
                        <span className = "darkpurple">let</span> <span className = "blue">multi</span> = <span className = "red">"Hallo"</span> <br/> 
                        <span className = "purple">print</span>(multi + "Wes")
                    </Fragment>}
                    result = "HalloWes" />

                <p>Remember string concatenation only works when combining <b>String</b> and <b>String</b>. You cannot
                   concat String and Int or other datatype together. In order to do that, you have to change the datatype 
                   of Int to string. (<a href = "/reference/swift/datatypes">Type Conversion</a></p>
                <SwiftCode 
                    code = {<Fragment>              
                        <span className = "darkpurple">let</span> <span className = "blue">age</span> = <span className = "blue">2</span> <br/> 
                        <span className = "purple">print</span>(<span className = "red">"My age is "</span> + <span className = "purple">String</span>(age))
                    </Fragment>}
                    result = "My age is 2" />

                <h3>String Interpolation</h3>
                <p>String interpolation is another way of combine String and String or other datatype together. 
                   The syntax is much more readable then the one shown in string concatenation.</p>
                   <SwiftCode 
                    code = {<Fragment>              
                        <span className = "darkpurple">let</span> <span className = "blue">age</span> = <span className = "blue">2</span> <br/> 
                        <span className = "purple">print</span>(<span className = "red">"My age is </span>\(<span className = "blue">age</span>)<span className = "red">"</span>)
                    </Fragment>}
                    result = "My age is 2" />
            </WithSidebar>
        </Fragment>
    )
};

export default String;
