import React, {Fragment} from "react";
import "./SwiftCode.css";

const SwiftCode = (props) => {
    return(
        <div>
            <div className = "topSwiftDiv">
                <p>Swift Code :</p>
            </div>
            <div className = {["btmSwiftDiv", 
                              props.result !== undefined ? null : "btmRadius"].join(' ')}>
                <code>{props.code}</code>
            </div>
            {(props.result !== undefined) ? <Fragment> <div className = "resultTopSwiftDiv">
                <p>Output :</p>
            </div>
            <div className = "resultSwiftDiv">
                <p className = "result">{props.result}</p>
            </div> </Fragment> : null }
        </div>
    )
};

export default SwiftCode;
