import React from 'react';
import classes from './image.module.css';

const image = (props) => {
    let imgSize = null;
    if (props.size === "W400") {
        imgSize = classes.W400;
    } else if (props.size === "W600"){
        imgSize = classes.W600;
    }
    return(
        <img src = {props.src}
             alt = {props.alt}
             class = {[classes.Img, imgSize].join(' ')}
                />
    )
};

export default image;