import React, {useState, Fragment} from 'react';
import {NavLink} from "react-router-dom";
import classes from './Navbar.module.css';
import Instagram from '../../Assets/Social Media Icon/Instagram.webp';
import FacebookIcon from '../../Assets/Social Media Icon/Facebook.png';
// import Linkedin from '../../Assets/Social Media Icon/Linkedin.webp';
import Youtube from '../../Assets/Social Media Icon/Youtube.png';

import Menu from '../UI/Menu/Menu';
import Logo from '../Logo/Logo';
import DropdownArrow from '../UI/Dropdown Arrow/DropdownArrow';
import Dropback from '../../Components/UI/dropback/dropback';

const Navabr = (props) => {
    const [activeReference, setActiveReference] = useState(false);
    const [activeCourses, setActiveCourses] = useState(false);
    const [showBackdrop, setShowBackdrop] = useState(false);

    const clickReference = () => {
        setActiveReference(!activeReference)
        setActiveCourses(false)
        setShowBackdrop(true)
    }

    // const clickComponents = () => {
    //     setActiveReference(false)
    //     setActiveComponents(!activeComponents)
    //     setActiveCourses(false)
    // }

    const clickCourses = () => {
        setActiveReference(false)
        setActiveCourses(!activeCourses)
        setShowBackdrop(true)
    }

    const dropbackClicked = () => {
        setActiveReference(false)
        setActiveCourses(false)
        setShowBackdrop(false)
    }

    return(
        <Fragment>
        <nav className = {classes.Navbar}>
            <Menu menuClicked = {props.menuClicked}/>
            <div className = {classes.leftFlex}>
                <div className = {classes.Logo}>
                    <Logo/>
                </div>
                <div className = {classes.Navitems}>
                    <ul>
                        <li>
                            <button onClick = {clickReference}>
                                <span className = {classes.mainButton}>Reference</span>
                                <DropdownArrow activate = {activeReference}/>
                                {activeReference ? <div className = {classes.dropdown}>
                                    <ul>
                                        <li><p className = {classes.SubHeader}>iOS Reference</p></li>
                                        <li><NavLink to = "/reference/swift" className = {classes.item}>Swift</NavLink></li>
                                        <li><NavLink to = "/reference/iosanimate/viewanimate" className = {classes.item}>iOS Animation</NavLink></li>
                                    </ul>
                                </div> : null }
                            </button>
                        </li>
                        {/* <li>
                            <button onClick = {clickComponents}>Components
                            <DropdownArrow activate = {activeComponents}/>
                            </button>
                        </li> */}
                        <li>
                            <button onClick = {clickCourses}>
                                <span className = {classes.mainButton}>Courses</span>
                                <DropdownArrow activate = {activeCourses}/>
                                {activeCourses ? <div className = {classes.dropdown}>
                                    <ul>
                                        <li><p className = {classes.SubHeader}>iOS Courses</p></li>
                                        <li><NavLink smooth to = "/course/fullios" className = {classes.item}> The Complete iOS App Developement Course</NavLink></li>
                                    </ul>
                                </div> : null}
                            </button>
                        </li>
                    </ul>
                </div>
            </div>
            <div className = {classes.rightFlex}>
                <ul>
                    <li><a href = "https://www.instagram.com/Appdevelopments.co/" target="_blank" rel="noreferrer"><img src = {Instagram} alt = "Apps Developement Instagram"/></a></li>
                    <li><a href = "https://www.facebook.com/appdevelopments.co/?view_public_for=106025394662939" target="_blank" rel="noreferrer"><img src = {FacebookIcon} alt = "Apps Developement Facebook"/></a></li>
                    {/* <li><a href = "https://www.linkedin.com/in/chuasweekwang/"><img src = {Linkedin} alt = "Apps Developement Linkedin"/></a></li> */}
                    <li><a href = "https://www.youtube.com/channel/UCT--knABW8IiFnBHs0O9otg" target="_blank" rel="noreferrer"><img src = {Youtube} alt = "Apps Developement Youtube"/></a></li>
                </ul>
            </div>
        </nav>
        {showBackdrop ? <Dropback dropbackClicked = {dropbackClicked}/> : null}
        </Fragment>
    )

};

export default Navabr;