import React, {Fragment} from "react";
import {Helmet} from "react-helmet";
import Constant from "../../../../../Constant";

import WithSidebar from "../../../../../Container/WithSidebar/WithSidebar";
import AuthorInfo from "../../../../../Components/AuthorInfo/AuthorInfo";
import Swift from "../Swift";
import SwiftCode from "../../../../../Components/SwiftCode/SwiftCode";

const ArithmeticOperations = (props) => {
    return(
        <Fragment>
            <Helmet>
                <title>Arithmetic Operations, Compound Assignment and Shift operations | Swift</title>
                <meta name="description" content="Arithmetic operations are used to turn a data into another 
                 piece of data. The simplest math operations are addition, subtraction, mutiplication and division. 
                 Moreover, you will also learn about Compound Assignment Operators, Increment and Decrement."/>
                <meta name="keywords" content="playground,overview,create,line,number,iOS,swift,tutorial,video,reference"/>
                <meta name="author" content="Wes Chua Swee Kwang"/>
                <link href={Constant.address + "/reference/swift/arithemtic"} rel="canonical"/>
                <link href={Constant.address} rel="home"/>
                <meta name="robots" content="index, follow"/> 
            </Helmet>

            <WithSidebar 
                data = {Swift}
                headerTitle = "Swift Reference"
                headerURL = "/reference/swift"
                activate = {[2,2]} // Section, Row
                state = {props.state}>

                <h1 className = "subHeader">Arithmetic Operations and Compound Assignment </h1>
                <h2 className = "header">Arithemtic Operations in Swift</h2>
                <AuthorInfo
                        version = "Xcode 12.2, Swift 5.3"/>
                <p>Arithmetic operations allow you to perform common mathematical operations in Swift like Addition (+), Subtraction (-),
                 Mutiplication (*), Division (/) and more. In this reference, we will cover :</p>
                <ol>
                    <li><a href = "/reference/swift/arithemtic#simple">Simple Arithmetic Operations (Add, Subtract, Multiply, Divide, Modulus)</a></li>
                    <li><a href = "/reference/swift/arithemtic#compound">Compound Assignment Operators (Increment and Decrement)</a></li>
                    {/* <li><a href = "/reference/swift/arithemtic#shift">Shift operations</a></li> */}
                </ol>
                <h3 href = "/reference/swift/arithemtic#simple">Simple Arithmetic Operations</h3>
                <p>Some of you might already know most of these operations as you learnt it during your school days.</p>
                <h4>Addition</h4>
                <SwiftCode 
                    code = {<Fragment>
                        <span className = "darkblue">12</span> + <span className = "darkblue">15</span><br/>
                    </Fragment>} 
                    result = {"27"}/>

                <h4>Subtraction</h4>
                <SwiftCode 
                    code = {<Fragment>
                        <span className = "darkblue">12</span> - <span className = "darkblue">20</span><br/>
                    </Fragment>} 
                    result = {"-8"}/>

                <h4>Mutiplication</h4>
                <SwiftCode 
                    code = {<Fragment>
                        <span className = "darkblue">20</span> * <span className = "darkblue">2</span><br/>
                    </Fragment>} 
                    result = {"30"}/>

                <h4>Division</h4>
                <p>In division, it is important to take note of the data type. The example shown below, 16.0 divide 3.0 is 
                   15.3333.... as you know. However, If both value are integer divide by integer such as 16 / 3, Swift will return 
                   an interger. Therefore, the resulted outcome is 15. Swift round down it. </p>
                <SwiftCode 
                    code = {<Fragment>
                        <span className = "darkblue">16.0</span> / <span className = "darkblue">3.0</span><br/>
                    </Fragment>} 
                    result = {"5.333333"}/>
                <br/>
                <SwiftCode 
                    code = {<Fragment>
                        <span className = "darkblue">16</span> / <span className = "darkblue">3</span><br/>
                    </Fragment>} 
                    result = {"5"}/>

                <h4>Modulus (Remainder)</h4>
                <p>The four operators above are easy to understand as you used it frequently in your life. 
                   Modulus or Remainder operator in Swift, return the remainder of the result when a division is done. For example,
                   16 % 3 = 5.33333. 5.33333 - 5 = 0.33333. 0.33333 * 3 = 1</p>
                <p>To simpify it, 3 goes into 16 four times with a remainder of 1. </p>
                <p>For example, </p>
                <SwiftCode 
                    code = {<Fragment>
                        <span className = "darkblue">16</span> % <span className = "darkblue">3</span><br/>
                    </Fragment>} 
                    result = {"1"}/>
                
                <h4>Note: Whitespace</h4>
                <p>In Swift, the whitespace of the left and right of the operator must be the same. Meaning if you put,
                   space before the operator, after also need an whitespace and vice versa. If not, it will result in error</p>
                <SwiftCode 
                    code = {<Fragment>
                        <span className = "darkblue">16</span> /<span className = "darkblue">3</span><br/>
                    </Fragment>} 
                    result = {"error: consecutive statements on a line must be separated by ';'"}/>
                <p>You are only allowed to do one of the following stated below: (Spaces or no Spaces)</p>
                <SwiftCode 
                    code = {<Fragment>
                        <span className = "darkblue">16</span> / <span className = "darkblue">3</span><br/>
                        <span className = "darkblue">16</span>/<span className = "darkblue">3</span>
                    </Fragment>} />

                <br/>
                <h3 href = "/reference/swift/arithemtic#compound">Compound Assignment Operators</h3>
                <p>Compound Assignment Operators combine assignment operator (=) with another operators such as 
                   arithemtic operators. Take a look at the follow example below: </p>
                <p>level += 1 is the same as level = level + 1. Meaning addition is done first by doing 1 + 1 and the 
                   resulted outcome 2 is then assign back to level.</p>
                <SwiftCode 
                    code = {<Fragment>
                        <span className = "darkpurple">var</span> <span className = "blue">level</span> = <span className = "darkblue">1</span> <br/>
                        level += <span className = "darkblue">1</span>
                    </Fragment>} />

                <br/>
                {/* <h3 href = "/reference/swift/arithemtic#shift">Shift operations</h3> */}

       
 
            </WithSidebar>
        </Fragment>
    )
};

export default ArithmeticOperations;
