import FilledButton from "../UI/Button/FilledButton/FilledButton";


const Box = (props) => {
    return (
        <div className = {classes.Boxdiv}>
            <h2>{props.header}</h2>
            <h3>{props.subheader}</h3>
            <p>{props.smalltext}</p>
            <br/>
            <FilledButton buttonTitle = {props.buttonTitle} buttonhref = {props.buttonhref}/>
            <img src = {props.img} alt = {props.imgAlt}/>
        </div>

    )
};

export default Box;