import React, {Fragment} from "react";
import {Helmet} from "react-helmet";
import Constant from "../../../../../Constant";

import WithSidebar from "../../../../../Container/WithSidebar/WithSidebar";
import AuthorInfo from "../../../../../Components/AuthorInfo/AuthorInfo";
import Swift from "../Swift";
import SwiftCode from "../../../../../Components/SwiftCode/SwiftCode";

const Comment = (props) => {
    return(
        <Fragment>
            <Helmet>
                <title>How to create Swift Comment? | Swift</title>
                <meta name="description" content="Comment is important to make your code easy to understand. 
                In Swift, you can create comment by using double slash or you can create mutiple line comments 
                by using asterisk and slash."/>
                <meta name="keywords" content="comment,documentation,create,iOS,swift,tutorial,reference"/>
                <meta name="author" content="Wes Chua Swee Kwang"/>
                <link href={Constant.address + "/reference/swift/comments"} rel="canonical"/>
                <link href={Constant.address} rel="home"/>
                <meta name="robots" content="index, follow"/> 
            </Helmet>

            <WithSidebar 
                data = {Swift}
                headerTitle = "Swift Reference"
                headerURL = "/reference/swift"
                activate = {[2,1]} // Section, Row
                state = {props.state}>

                <h1 className = "subHeader">How to create Swift Comment and what is it for? </h1>
                <h2 className = "header">Swift Comment</h2>
                <AuthorInfo
                        version = "Xcode 12.2, Swift 5.3"/>

                <p>Comment allows you to doument your code and tell other developer or future you what the code 
                    means when you look back on it. This help you understand why you wrote your code in this way
                    or what problem are u solving. </p>
                <p>Swift comiler will only run Swift Code and ignore all the comments you write inside the 
                   application. In this comment reference the following topic will be cover: </p>
                   <ol>
                       <li><a href = "/reference/swift/comments#single">How to create a single line comment?</a></li>
                       <li><a href = "/reference/swift/comments#multi">How to create a multi-line comment?</a></li>
                       <li><a href = "/reference/swift/comments#good">How to write a good comment?</a></li>
                   </ol>
                <h3 id = "single">How to create a single line comment?</h3>
                <SwiftCode 
                    code = {<Fragment>
                         {/* eslint-disable-next-line  */}
                        <span classNam = "grey">// This is a single line comment</span> <br/>  
                         
                        <span className = "darkpurple">var</span> <span className = "blue">str</span> = <span className = "red">"Appdevelopments.co"</span> 
                        {/* eslint-disable-next-line  */}
                        <span classNam = "grey">// This is a single line comment on the side</span>  <br/>
                        print(str)
                    </Fragment>} 
                    result = {"Appdevelopments.co"}/>
                <h3 id = "multi">How to create a multi-line comment? </h3>
                <SwiftCode 
                    code = {<Fragment>
                        <span classNam = "grey">
                        /* <br/>
                        &nbsp;This <br/>
                        &nbsp;is a mutiple <br/>
                        &nbsp;line comment <br/>
                        */ <br/>
                        </span>
                        <span className = "darkpurple">var</span> <span className = "blue">str</span> = <span className = "red">"Appdevelopments.co"</span> <br/>
                        print(str)
                    </Fragment>} 
                    result = {"Appdevelopments.co"}/>

                <h3 id = "good">How and when to write a good comment? </h3>
                <p>Inaccurate comments are worse than no comments at all. 
                  As they can mislead someone understanding of a code. Writting 
                  unnecessary comments can create a mess in your script. In this section,
                  we will be covering when and how you should write a comments.</p>

                <h3>DON'Ts </h3>
                <p><strong>Commenting a bad code: </strong> Sometime you know that your 
                  code is messy and even you, yourseleves has a hard time to understand. 
                  Rather then writing a long comment to explain your code, you should clean 
                  up your code instead!</p>
                <p><strong>Redundant Comments: </strong> You should not comment on something that
                   already means sense or you code explained itself. Writing this comment make your script messy.
                   For example, </p>
                   <SwiftCode 
                    code = {<Fragment>
                         {/* eslint-disable-next-line  */}
                        currentLevel += <span className = "darkblue">1</span> <span classNam = "grey">// This increase the current level by one</span>  <br/>      
                    </Fragment>} />
                <p><strong>Function can explain: </strong> Do not write a comment if you can convert the code
                   into a function which the function is able to explain itself. For example, </p>
                   <SwiftCode 
                    code = {<Fragment>
                        {/* eslint-disable-next-line  */}
                        <span classNam = "grey">// This check if a user is an new user or not </span> <br/>  
                        <span className = "darkpurple">if</span> (acc.currentLevel == 0)  &#123;<br/>      
                        &nbsp; ...... <br/>
                        &#125;
                    </Fragment>} />
                    <p>Changing it to a simple method help you to understand the code without any comments.</p>
                    <SwiftCode 
                    code = {<Fragment>
                        <span className = "darkpurple">if</span> (acc.isNewUser)  &#123;<br/>      
                        &nbsp; ...... <br/>
                        &#125;
                    </Fragment>} />
                <h3>DOs</h3>
                <p><strong>Informative comments: </strong> Sometimes it is impossible to use a function 
                   to convery the meaning of the code. It is better to write a informative and descriptive 
                   comments for you and other developers to see.</p>
                <p><strong>Intention comments: </strong> Writing what the code will be doing and the intented 
                   or expected results. If it is a function, you can also write the expected outcome of it.</p>
                <p><strong>Warning comments: </strong> Sometimes it is better to warn other developer of the 
                   consequences of running the following code or to tell other developers of the possible of 
                   crushing the application.</p>
                <p><strong>Expected input (For methods): </strong> For a function, you can comments on the 
                   type of parameters are expected to input and their datatypes.</p>
            </WithSidebar>
        </Fragment>
    )
};

export default Comment;
